<template>
  <div class="fillcontain">
    <h3>批次管理</h3>
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="12">
        <el-button class="but1 search" type="primary" @click="tableShowWay(false)">批次列表</el-button>
        <el-button type="primary" class="search" @click="tableShowWay(true)">新增批次</el-button>
      </el-col>
    </el-row>
    <div v-show="tableShow==false">
      <el-table
        v-loading="loading"
        :data="tableData"
        border
        stripe
        highlight-current-row
        header-cell-class-name="table-header-class"
        style="width:100%"
      >
        <el-table-column label="序号" align="center">
          <template slot-scope="scope">
            <span>{{scope.$index+(paginations.pageIndex-1) * paginations.pageSize + 1}}</span>
          </template>
        </el-table-column>
        <el-table-column property="name" label="批次名称" align="center"></el-table-column>
        <el-table-column property="updateTime" label="时间" :formatter="formatTime" align="center"></el-table-column>
        <el-table-column fixed="right" label="操作">
          <template slot-scope="scope">
            <el-button @click="seeClick(scope.row.vs)" type="primary" size="small">查看</el-button>
            <el-button @click="editClick(scope.row)" type="primary" size="small">编辑</el-button>
            <el-button @click="handleClick(scope.row)" type="primary" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-row>
        <el-col :span="24">
          <div class="pagination">
            <el-pagination
              v-if="paginations.total > 0"
              :page-sizes="paginations.pageSizes"
              :page-size="paginations.pageSize"
              :layout="paginations.layout"
              :total="paginations.total"
              :current-page="paginations.pageIndex"
              @current-change="handleCurrentChange"
              @size-change="handleSizeChange"
            ></el-pagination>
          </div>
        </el-col>
      </el-row>
    </div>
    <div v-show="tableShow==true" style="padding:30px 0 0 100px;color:#fff;">
      <el-row>
        <el-col :span="24">
          <el-form :model="form">
            <el-form-item label="批次名字：" :label-width="formLabelWidth">
              <el-input v-model="form.name" autocomplete="off" style="width:250px;"></el-input>
            </el-form-item>
            <el-form-item label="设定时间：" :label-width="formLabelWidth">
              <!-- <el-input v-model="form.name" autocomplete="off" style="width:250px;"></el-input> -->
              <el-date-picker
                value-format="timestamp"
                dateType="time"
                v-model="form.time"
                placeholder="选择日期"
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="对垒双方：" :label-width="formLabelWidth">
              <p class="pcBg"></p>
              <ul>
                <li v-for="(item, index) in inputArr" :key="index">
                  <br />
                  <el-cascader
                    v-for="(item1, index) in item.sgroup"
                    :key="index"
                    class="vsWrapper inputCascader"
                    :options="optionsVS"
                    @change="handleAreaChange"
                    v-model="item1.vs1"
                    :props="props"
                    clearable
                    style="width:185px;"
                  ></el-cascader>
                  <span class="cascaderWrapper">
                    <span class="cascaderSpan" v-on:click="cascaderAdd(1,item)">+</span>
                    <span class="cascaderSpan" v-on:click="cascaderAdd(0,item)">-</span>
                  </span>VS
                  <el-cascader
                    @change="handleAreaChange2"
                    class="vsWrapper inputCascader"
                    v-for="(item1, index1) in item.tgroup"
                    :key="'info2-'+index1"
                    :options="optionsVS"
                    v-model="item1.vs2"
                    :props="props"
                    style="width:185px;"
                    clearable
                  ></el-cascader>
                  <span class="cascaderWrapper">
                    <span class="cascaderSpan" v-on:click="cascaderAdd2(1,item)">+</span>
                    <span class="cascaderSpan" v-on:click="cascaderAdd2(0,item)">-</span>
                  </span>&nbsp&nbsp
                  <div v-for="(items, index) in item.combat">
                    <el-date-picker
                      v-model="items.time"
                      @change="changeDate(items)"
                      type="datetimerange"
                      range-separator="至"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                    ></el-date-picker>
                    <el-radio class="vsradio" v-model="items.winner" label="0">胜</el-radio>
                    <el-radio class="vsradio" v-model="items.winner" label="1">负</el-radio>
                    <el-radio class="vsradio" v-model="items.winner" label="2">平</el-radio>
                    <span class="cascaderWrapper">
                      <span class="cascaderSpan" v-on:click="cascaderAdd3(1,item.combat)">+</span>
                      <span class="cascaderSpan" v-on:click="cascaderAdd3(0,item.combat)">-</span>
                    </span>
                    <!-- <el-button @click="okvs">确定</el-button> -->
                  </div>
                </li>
              </ul>
              <el-button size="mini" class="button" @click="handleAdd" plain>＋</el-button>
              <el-button size="mini" class="button" @click="removeInput" plain>－</el-button>
            </el-form-item>
            <el-form-item label="选择文件：" :label-width="formLabelWidth">
              <input type="file" name="aatraj" id="aatraj" multiple />
            </el-form-item>
            <!-- <el-form-item label="planeState--文件位置：" :label-width="formLabelWidth">
              <input type="file" name="planeState" id="planeState" />
            </el-form-item>
            <el-form-item label="rtkn--文件位置：" :label-width="formLabelWidth">
              <input type="file" name="rtkn" id="rtkn" />
            </el-form-item>
            <el-form-item label="tspi--文件位置：" :label-width="formLabelWidth">
              <input type="file" name="tspi" id="tspi" />
            </el-form-item>
            <el-form-item label="hitResult--文件位置：" :label-width="formLabelWidth">
              <input type="file" name="hitResult" id="hitResult" />
            </el-form-item>-->
          </el-form>
          <div style="margin-left: 119px;margin-top: 50px;">
            <el-button @click="tableShowWay(false)" class="search2">取 消</el-button>
            <el-button type="primary" :loading="disabledType" @click="addOk" class="search2">确定</el-button>
          </div>
        </el-col>
      </el-row>
    </div>

    <el-dialog title="详情" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column property="sUser" label="对阵双方" width="270"></el-table-column>
        <el-table-column property="tUser" label="对阵双方" width="270"></el-table-column>
        <el-table-column property="start" label="开始时间" width="150" :formatter="formatState"></el-table-column>
        <el-table-column property="end" label="结束时间" width="150" :formatter="formatEnd"></el-table-column>
        <el-table-column property="winner" label="比赛结果" width="80" :formatter="formatWinner"></el-table-column>
      </el-table>
    </el-dialog>
    <el-dialog title="修改批次信息" :visible.sync="dialogFormVisible2" class="customWidth2">
      <el-form :model="form">
        <el-form-item label="批次名称" :label-width="formLabelWidth">
          <el-input v-model="editForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <!-- <el-form-item label="数据状态" :label-width="formLabelWidth">
          <el-select v-model="editForm.state" placeholder="请选择活动区域">
            <el-option label="有效" value="1"></el-option>
            <el-option label="无效" value="0"></el-option>
          </el-select>
        </el-form-item>-->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible2 = false">取 消</el-button>
        <el-button type="primary" @click="editKo">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { batchList, add, modify } from "@/api/batch";
import axios from "axios";
import { getToken } from "@/utils/auth";
import QS from "qs";
export default {
  data() {
    return {
      editForm: {
        name: "",
        state: ""
      },
      disabledType: false,
      fileList: [],
      value1: [],
      dialogTableVisible: false,
      dialogTableVisible2: false,
      gridData: [],
      tableShow: false,
      inputArr: [
        {
          sgroup: [{ vs1: "" }],
          tgroup: [{ vs2: "" }],
          combat: [{ time: "", winner: 3 }]
        }
      ],
      sgroup: [{ vs1: "", vs2: "", radio: 1, time: "" }],
      tgroup: [{ vs1: "", vs2: "", radio: 1 }],
      optionsVS: [],
      props: {
        multiple: false,
        checkStrictly: true,
        value: "_id",
        label: "name"
      },
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        vs: [],
        type: [],
        vs1: [],
        vs2: [],
        resource: "",
        desc: ""
      },
      dialogFormVisible: false,
      dialogFormVisible2: false,
      formLabelWidth: "168px",
      formLabelWidth2: "120px",
      tableData: [],
      loading: false,
      batchData: null,
      //需要给分页组件传的信息
      paginations: {
        total: 0, // 总数
        pageIndex: 1, // 当前位于哪页
        pageSize: 20, // 1页显示多少条
        pageSizes: [5, 10, 15, 20], //每页显示多少条
        layout: "total, sizes, prev, pager, next, jumper" // 翻页属性
      },
      vs1: {},
      vs2: {}
    };
  },
  created() {},
  mounted() {
    this.init();
    this.batchList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      search: {
        state: 1
      }
    });
  },
  methods: {
    tableShowWay(bool) {
      this.tableShow = bool;
      if (!bool) {
        for (var i in this.form) {
          this.form[i] = "";
        }
        this.inputArr = [
          {
            sgroup: [{ vs1: "" }],
            tgroup: [{ vs2: "" }],
            combat: [{ time: "", winner: 3 }]
          }
        ];
      }
    },

    editClick(row) {
      this.dialogFormVisible2 = true;
      this.editForm = row;
    },
    editKo() {
      modify({
        _id: this.editForm._id,
        name: this.editForm.name,
        state: this.editForm.state
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "修改批次信息成功！",
            type: "success"
          });
          this.batchList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: 1
            }
          });
        }
      });
    },
    handleClick(row) {
      modify({
        _id: row._id,
        state: 0
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "删除批次成功！",
            type: "success"
          });
          this.batchList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: 1
            }
          });
        }
      });
    },
    init() {
      var plane = this.sessionData("get", "planeList");
      var user = this.sessionData("get", "userList");
      plane.forEach(i => {
        i.disabled = true;
        i.children = [
          { _id: "0", name: "前舱" },
          { _id: "1", name: "后舱" }
        ];
      });
      user.forEach(i => {
        i.disabled = true;
        i.children = plane;
      });
      this.optionsVS = user;
    },
    changeDate(i) {
      i.start = i.time[0].getTime();
      i.end = i.time[1].getTime();
    },
    handleAreaChange(val) {
      if (!val) return;
      this.vs1[val[1] + "," + val[2]] = val;
      for (var i in this.vs1) {
        if (this.vs1[i][0] == val[0]) {
          delete this.vs1[i];
          this.vs1[val[1] + "," + val[2]] = val;
        }
      }
      console.log("就是这里的数据", this.vs1);
    },
    handleAreaChange2(val) {
      if (!val) return;
      this.vs2[val[1] + "," + val[2]] = val;
      for (var i in this.vs2) {
        if (this.vs2[i][0] == val[0]) {
          delete this.vs2[i];
          this.vs2[val[1] + "," + val[2]] = val;
        }
      }
      console.log("就是这里的数据", this.vs2);
    },
    seeClick(row) {
      console.log("查看这一行数据", row);
      var data = [];
      for (var i in row) {
        for (var j in row[i].combat) {
          var sUser = "";
          var tUser = "";
          for (var k in row[i].sgroup) {
            sUser +=
              this.batchData.users[row[i].sgroup[k].uid] ||
              "" + "（前）" + this.batchData.users[row[i].sgroup[k].hid] ||
              "" + "（后）" + this.batchData.planes[row[i].tgroup[k].fid];
          }
          for (var k in row[i].tgroup) {
            tUser +=
              this.batchData.users[row[i].tgroup[k].uid] ||
              "" + "（前）" + this.batchData.users[row[i].tgroup[k].hid] ||
              "" + "（后）" + this.batchData.planes[row[i].tgroup[k].fid];
          }
          row[i].combat[j].sUser = sUser;
          row[i].combat[j].tUser = tUser;
          data.push(row[i].combat[j]);
        }
      }
      console.log("查看这一行的数据", data);
      this.gridData = data;
      this.dialogTableVisible = true;
    },
    formatTime(row) {
      return this.$tool.formatDate(new Date(row.time));
    },
    formatWinner(row) {
      return row.winner == 0 ? "胜" : row.winner == 1 ? "负" : "平";
    },

    formatState(row) {
      return this.$tool.formatDate(new Date(row.start));
    },
    formatEnd(row) {
      return this.$tool.formatDate(new Date(row.end));
    },
    batchList(obj) {
      var data = this.$tool.deep(obj);
      data.index -= 1;
      batchList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          this.loading = false;
          this.paginations.total = list.total;
          this.tableData = list.array;
          this.batchData = list;
          console.log("批次列表", list);
        }
      });
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },

    cascaderAdd(type, i) {
      if (type) {
        i.sgroup.push({ value: "" });
      } else {
        i.sgroup.pop();
      }
    },

    cascaderAdd2(type, i) {
      if (type) {
        i.tgroup.push({ value: "" });
      } else {
        i.tgroup.pop();
      }
    },

    cascaderAdd3(type, i) {
      if (type) {
        i.push({ time: "", winner: "3" });
      } else {
        i.pop();
      }
    },

    okvs() {
      var plane = this.sessionData("get", "planeList");
      var sgroup = this.getGroup(1);
      var tgroup = this.getGroup(2);
      this.sgroup = [];
      this.tgroup = [];
    },

    addOk() {
      var plane = this.sessionData("get", "planeList");
      var sgroup = this.getGroup(1);
      var tgroup = this.getGroup(2);

      var inputArr = this.$tool.deep(this.inputArr);

      for (var i in inputArr) {
        var fjidArr = [];
        var fjidArr2 = [];
        var sg = [];
        var tg = [];
        for (var j in inputArr[i].sgroup) {
          if (inputArr[i].sgroup[j].vs1) {
            var fjid = inputArr[i].sgroup[j].vs1[1];
            fjidArr.push(fjid);
          }
        }
        var newArr = new Set(fjidArr);
        var fin = Array.from(newArr);

        for (var j in inputArr[i].tgroup) {
          if (inputArr[i].tgroup[j].vs2) {
            var fjid2 = inputArr[i].tgroup[j].vs2[1];
            fjidArr2.push(fjid2);
          }
        }
        var newArr2 = new Set(fjidArr2);
        var fin2 = Array.from(newArr2);

        fin.forEach(i => {
          sg.push(sgroup[i]);
        });

        fin2.forEach(i => {
          tg.push(tgroup[i]);
        });

        inputArr[i].sgroup = sg;
        inputArr[i].tgroup = tg;
      }

      var form = this.$tool.deep(this.form);

      if (form.time) {
        try {
          form.time = form.time.getTime();
        } catch (e) {}
      }
      var data = {
        name: form.name,
        time: form.time,
        vs: inputArr
      };
      this.uploadKo(inputArr);
    },

    uploadKo(data) {
      var aatraj = document.getElementById("aatraj").files[0];
      var planeState = document.getElementById("aatraj").files[1];
      var rtkn = document.getElementById("aatraj").files[2];
      var tspi = document.getElementById("aatraj").files[3];
      var hitResult = document.getElementById("aatraj").files[4];
      var hitResult2 = document.getElementById("aatraj").files[5];

      var formData = new FormData();
      formData.append("multiple", aatraj);
      formData.append("multiple", planeState);
      formData.append("multiple", rtkn);
      formData.append("multiple", tspi);
      formData.append("multiple", hitResult);
      formData.append("multiple", hitResult2);

      var form = this.$tool.deep(this.form);

      formData.append("vs", JSON.stringify(data));
      formData.append("name", form.name);
      formData.append("time", this.form.time);
      formData.append("token", getToken("Token"));
      this.disabledType = true;
      var url =
        window.baseURL ||
        sessionStorage.getItem("serverUrl_str") ||
        "http://127.0.0.1:8088";
      axios
        .post(url + "/batch/add", formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            this.tableShow = false;
            this.$message({
              message: "新增批次成功！",
              type: "success"
            });
            this.tableShowWay(false)
          } else {
            this.$notify({
              title: "警告",
              message: res.data.message,
              type: "warning"
            });
          }
          this.batchList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: 1
            }
          });
          this.disabledType = false;
        })
        .catch(res => {
          this.disabledType = false;
        });
    },

    getGroup(t) {
      var plane = this.sessionData("get", "planeList");
      var group = {};
      plane.forEach(i => {
        var arr = {};
        if (this["vs" + t][i._id + "," + "0"]) {
          arr.uid = this["vs" + t][i._id + "," + "0"][0]; //前仓
          if (this["vs" + t][i._id + "," + "1"]) {
            arr.hid = this["vs" + t][i._id + "," + "1"][0]; //后仓
          }
          arr.fid = i.fid; //前仓
          group[i._id] = arr;
        }
      });
      return group;
    },

    getGroup2(t) {
      var plane = this.sessionData("get", "planeList");
      var group = [];
      plane.forEach(i => {
        var arr = {};
        if (this["vs" + t][i._id + "," + "0"]) {
          arr.uid = this["vs" + t][i._id + "," + "0"][0]; //前仓
          if (this["vs" + t][i._id + "," + "1"]) {
            arr.hid = this["vs" + t][i._id + "," + "1"][0]; //后仓
          }
          arr.fid = i.fid; //前仓
          group.push(arr);
        }
      });
      return group;
    },

    handleAdd() {
      this.inputArr.push({
        sgroup: [{ vs1: "", vs2: "", radio: 1, time: "" }],
        tgroup: [{ vs1: "", vs2: "", radio: 1, time: "" }],
        combat: [{ time: "", winner: 3 }],
        vs1: "",
        vs2: "",
        radio: 1,
        time: ""
      });
    },

    removeInput() {
      this.inputArr.pop();
    },

    submitUpload() {
      this.$refs.upload.submit();
    },

    handlePreview(file) {
      console.log(file);
    },

    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.batchList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: 1
        }
      });
    },
    // 上下分页
    handleCurrentChange(page) {
      this.paginations.pageIndex = page;
      this.batchList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: 1
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "./table.less";
input[type="file"] {
  background: #06a4c5;
  border-radius: 3px;
  width: 160px;
}

/deep/.el-form-item__label {
  color: #52e5ff !important;
}
/deep/.el-cascader .el-input .el-input__inner {
  color: #fff !important;
}
/deep/.el-form-item {
  margin-bottom: 5px;
}
.pcBg {
  background: url("../../assets/img/pc.png") no-repeat 45px 0px;
  width: 748px;
  height: 108px;
}
/deep/.el-dialog {
  background: #055586 !important;
}
/deep/.el-dialog__title {
  color: #fff;
}
.search2 {
  margin-top: 11px;
  background: #04b3e0;
  border: solid 1px #03d0fb;
  color: #fff;
  border-radius: 0px;
}
.search {
  margin-top: 11px;
  background: #04b3e0;
  border: solid 1px #03d0fb;
  border-radius: 0px;
}
.search:first-child {
  margin-left: 30px;
}
.order_list {
  margin-bottom: 30px;
}
/deep/.el-input__inner {
  border: solid 1px #52e5ff !important;
}
/deep/.el-radio__label {
  color: #52e5ff !important;
}
.cascaderSpan {
  width: 15px;
  height: 15px;
  background: #52e5ff;
  display: block;
  line-height: 15px;
  text-align: center;
  cursor: pointer;
}
.cascaderWrapper {
  display: inline-block;
  position: relative;
  top: 7px;
}
.vsWrapper {
  width: 250px;
}
.fillcontain {
  padding-bottom: 0;
  background: url("../../assets/img/bbg.png") no-repeat -2px 0px;
  width: 1648px;
  height: 984px;
  margin: 20px;
}
